#root {
    position: relative;
    width: 100vw;
    min-height: 100vh;
}

.btn-sing-out {
    margin-left: auto;
    display: block;
    margin-bottom: 10px;
}

.img-avatar {
    border-radius: 50%;
    margin: 5px;
}

.btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}